@charset "UTF-8";
/*все переменные и моды */
*::-webkit-input-placeholder {
  color: #666;
  opacity: 1; }

*:-moz-placeholder {
  color: #666;
  opacity: 1; }

*::-moz-placeholder {
  color: #666;
  opacity: 1; }

*:-ms-input-placeholder {
  color: #666;
  opacity: 1; }

*, *:before, *:after {
  box-sizing: border-box; }

html {
  width: 100%;
  font-size: 16px;
  line-height: 1.2;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden; }

body {
  font-family: Georgia, serif;
  font-size: 16px;
  line-height: 1.2;
  position: relative;
  color: #666;
  -webkit-backface-visibility: hidden;
  overflow-x: hidden;
  -webkit-font-smoothing: antialised; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

a {
  outline: 0; }

.hidden {
  display: none; }

/*все переменные и моды */
header {
  background: #eba35d;
  /* Old browsers */
  background: -moz-linear-gradient(top, #d19869 0%, #ffe3b4 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #d19869 0%, #ffe3b4 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #d19869 0%, #ffe3b4 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d19869', endColorstr='#ffe3b4',GradientType=0 );
  /* IE6-9 */
  padding: 20px 0; }

header h1 {
  color: #ffffff;
  font-size: 1.625rem;
  text-align: center;
  margin-bottom: 30px; }

header h1 span {
  text-transform: uppercase; }

header h2 {
  color: #c97019;
  text-align: center;
  font-size: 1.5rem; }

.block {
  padding: 20px 0; }

.block_cover, .block_text {
  float: left;
  width: 50%; }

.block_cover {
  text-align: center; }

.block_cover_autor {
  padding-right: 50px; }

.block_cover img {
  width: 100%;
  height: auto; }

h3 {
  display: inline-block;
  font-size: 1.5rem;
  color: #eba35d;
  position: relative;
  margin: 0 10px 30px; }

.block_text h3:before {
  position: absolute;
  content: '';
  border: 1px solid #eba35d;
  bottom: 0;
  width: 100%; }

.block_text p {
  font-size: 1.25rem; }

.block_text p span {
  font-size: 0.6875rem; }

.block_text a {
  color: #666;
  -moz-transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

.block_text a:hover {
  color: #666;
  text-decoration: none; }

.block_list {
  text-align: center; }

.block_list h3 {
  color: #000; }

.list {
  padding-left: 0;
  display: inline-block;
  list-style-type: none; }

.list li {
  text-align: left;
  padding: 5px 0 15px 0;
  margin: 10px 0;
  position: relative;
  border-bottom: 1px dashed #40D9E0; }

.list li:before {
  font-size: 1.35rem;
  font-family: FontAwesome;
  top: 10px;
  position: absolute;
  left: -30px;
  content: "\f0a4";
  line-height: 1;
  color: #eba35d; }

.polit_termes {
  text-align: left; }

.polit_termes span {
  font-weight: bold; }

.polit_list {
  text-align: left; }

ol {
  font-weight: bold; }

.polit_list li {
  font-weight: normal;
  margin-top: 5px; }

.polit_list li span {
  font-weight: bold; }

.polit_list li:first-child {
  margin-top: 10px; }

.polit_list li:last-child {
  margin-bottom: 10px; }

footer {
  padding: 20px 0;
  background-color: #000;
  color: #fff; }

.polit_list li.adress span {
  display: block;
  font-weight: normal; }

li.adress {
  list-style: none; }

.footer_box {
  width: 33.3333%;
  float: left; }

.footer_box ul {
  list-style-type: none; }

.footer_box ul li a {
  color: #fff;
  text-decoration: none;
  -moz-transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

.footer_box ul li a:hover {
  text-decoration: underline; }

/*стили кнопки наверх*/
.top {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 40px;
  line-height: 40px;
  background-color: #eba35d;
  cursor: pointer;
  text-align: center;
  opacity: .3;
  z-index: 3;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease;
  color: white; }

.top .button_up {
  color: #eba35d;
  font-size: 1rem;
  border: 0;
  padding: 5px 10px;
  cursor: pointer; }

.top:hover {
  opacity: 1; }

.top.active {
  bottom: -60px; }

/*все переменные и моды */
/*все переменные и моды */
/* стили формы обратной связи */
.forms-call {
  font-size: .875rem; }

.forms-call label {
  margin: 10px 0;
  display: block; }

.forms-call input {
  display: block;
  width: 100%;
  height: 40px;
  font-size: 0.938rem;
  line-height: 40px;
  border: 1px solid #000;
  border-radius: 3px;
  text-indent: 10px;
  margin: 10px auto;
  color: #000; }

.forms-call input:focus {
  border: 1px solid #eba35d; }

.forms-call .get-buttons {
  margin: 10px 0 10px 5px;
  background-color: #f2001D;
  padding: 4px 40px;
  line-height: 50px;
  font-size: 1rem;
  text-decoration: none;
  border: 0;
  color: #fff;
  letter-spacing: 1px;
  -moz-transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

.button-call {
  text-align: right;
  margin: 0;
  float: right; }

.forms-call .get-buttons:focus,
.forms-call .get-buttons:hover,
.forms-call .get-buttons:active {
  background-color: #e58930; }

/* конец формы обратной связи */
/*оформление всплывающей формы а ниже и благодарности */
.forms-calldecor {
  max-width: 480px;
  position: relative;
  background-color: rgba(255, 255, 255, 0.99);
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s else;
  transition: all 0.5s else;
  overflow: hidden;
  margin: auto;
  border: 1px solid #fff;
  border-radius: 10px; }

.forms-calldecor img {
  width: 100%;
  height: auto; }

.forms-calldecor h4 {
  color: #000;
  text-align: center;
  font-size: 30px;
  margin: 30px 0 40px 0; }

.forms-calldecor input {
  width: 90%;
  color: #333333; }

.forms-calldecor .button-call {
  float: none;
  text-align: center;
  color: #fff;
  margin-bottom: 20px; }

.forms-calldecor .mfp-close {
  position: absolute;
  right: 15px;
  font-size: 30px;
  top: 15px;
  border-radius: 3px;
  -moz-transition: all .75s ease;
  -webkit-transition: all .75s ease;
  transition: all .75s ease; }

.forms-calldecor .mfp-close:hover {
  color: red;
  -moz-transform: rotate(45deg);
  /* Firefox */
  -o-transform: rotate(45deg);
  /* Opera */
  -webkit-transform: rotate(45deg);
  /* Safari y Chrome */
  filter: progid:DXImageTransform.Microsoft.Matrix(sizingMethod='auto expand', M11=0.7071067811865476, M12=-0.7071067811865475, M21=0.7071067811865475, M22=0.7071067811865476);
  /* IE */ }

.forms-calldecor .mfp-close-btn-in .mfp-close {
  color: #fff;
  font-size: 22.5em;
  font-weight: bold;
  border-radius: 3px; }

.mfp-bg {
  background: #fff; }

.forms-calldecor .success {
  color: #fff;
  background-color: #7cc15a;
  position: absolute;
  z-index: 1;
  left: 0;
  top: -100%;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  opacity: 0;
  padding-right: 20px;
  padding-left: 20px;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.forms-calldecor .success.active {
  top: 0;
  opacity: 1; }

.forms-calldecor .success p {
  text-transform: uppercase;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-self: center;
  -ms-flex-item-align: center;
  align-self: center;
  color: #fff;
  font-size: 20px;
  margin: auto;
  text-align: center; }

/* overlay at start */
.mfp-fade.mfp-bg {
  opacity: 0;
  -webkit-transition: all 0.615s ease-out;
  -o-transition: all 0.615s ease-out;
  transition: all 0.615s ease-out; }

/* overlay animate in */
.mfp-fade.mfp-bg.mfp-ready {
  opacity: 0.8; }

/* overlay animate out */
.mfp-fade.mfp-bg.mfp-removing {
  opacity: 0; }

/* content at start */
.mfp-fade.mfp-wrap .mfp-content {
  opacity: 0; }

/* content animate it */
.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
  opacity: 1;
  -webkit-transition: all 0.615s ease-out;
  -o-transition: all 0.615s ease-out;
  transition: all 0.615s ease-out;
  -webkit-transform: translateY(1%);
  -ms-transform: translateY(1%);
  transform: translateY(1%); }

/* content animate out */
.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
  opacity: 0;
  -webkit-transform: translateY(-120%);
  -ms-transform: translateY(-120%);
  transform: translateY(-120%); }

